<template>
	<view class="container">
		<view v-if="cartGoodsList.length>0">
			<scroll-view scroll-y="true" class="scroll-list wx-scroll-list">
				<view class="list">
					<view class="item"  v-for="(item,index) in cartGoodsList" :key="item.cart_goods_id"  @click="toDetail(item.goods.id)">
						<checkbox :value="String(item.cart_goods_id)" @click.stop="updateStatus(item.cart_goods_id)" :data-index="index" :checked="item.is_checked" color="#009000"></checkbox>
						<view class="content">
							<image :src="item.goods.image" alt="" class="img"></image>
							<view class="info">
								<view class="name line">{{item.goods.title}}</view>
								<view class="number-box">
									<text class="price">￥{{item.goods.price}}</text>
									<view @click.stop>
										<uni-number-box :min="0" :max="item.goods.stock"  :value="item.number"  @change="changeNumber($event,item)" />
									</view>
								</view>
							</view>
						</view>
					</view>
				</view>
			</scroll-view>
			<view class="bot-box">
				<view class="text-box">
					<checkbox-group @change="allChange">
						<checkbox :value="String(isAllChoose)" :checked="isAllChoose" color="#009000"></checkbox>
					</checkbox-group>
				    <text>{{isAllChoose?'不全选':'全选'}}</text>
				</view>
				<view class="r">
					<view class="price-box">
						<text class="text1">合计:</text>
						<text class="text2">￥</text>
						<text class="price">{{total.toFixed(2)}}</text>
					</view>
					<button class="btn" @click="submit">结算</button>
				</view>
			</view>
		</view>
		<view v-else class="no-content">
			<view class="content">
				<image src="../../static/icon/no.png" class="icon"></image>
				<view class="text">您还没有添加商品哦~</view>
			</view>
		</view>
	</view>
</template>

<script>
	import {
		mapState,
		mapMutations,
		mapActions,
		mapGetters
	} from 'vuex'
	export default {
		data() {
			return {
				
			}
		},
		onLoad() {
			this.setCartGoodsChecked(false)
		},
		computed: {
			...mapState(['cartGoodsList', 'userInfo']),
			...mapGetters(['total','isAllChoose'])
		},
		methods: {
			...mapMutations(['setCartGoodsList','setAllChoose']),
			...mapActions(['getCartGoodsList']),
			changeNumber(value,item){ //修改数字
			    if(value==0){
				  this.deleteCartGoods(item.cart_goods_id)
				}else{
					let data = {
						id:item.cart_goods_id,
						number:value
					}
					this.setCartGoodsNumber(data)
				}
			},
			updateStatus(id){ //修改商品状态
				this.setCartGoodsChecked(id)
			},
			//修改购物车中商品的数量
			async setCartGoodsNumber(data){  
				const response = await this.$api.cart.setCartGoodsNumber(data)
				if (response.data.code === 200) {
					this.getCartGoodsList(this.userInfo.id)
				}
			},
			// 修改购物车中商品状态
			async setCartGoodsChecked(id){
				const response = await this.$api.cart.setCartGoodsChecked(id)
				if (response.data.code === 200) {
					this.getCartGoodsList(this.userInfo.id)
				}
			},
			// 删除购物车中的商品
			async deleteCartGoods(id){
				const response = await this.$api.cart.deleteCartGoods(id)
				if (response.data.code === 200) {
					this.getCartGoodsList(this.userInfo.id)
				}
			},
			allChange(e){
				this.setAllChoose(e.detail.value[0])
				if(e.detail.value[0]){//全选
				  this.setCartGoodsChecked(true)
				}else{ //全不选
				  this.setCartGoodsChecked(false)
				}
			},
			toDetail(id){ //跳转商品详情
				uni.navigateTo({
					url:'/pages/goods/detail?goodsId='+id
				})
			},
			submit(){
				if(this.total==0){
					uni.showToast({
						title:'请选择商品',
						icon:'error'
					})
					
				}else{
					uni.navigateTo({
						url:'/pages/order/submit'
					})
				}
			}
		},
		onShow() {
			this.getCartGoodsList(this.userInfo.id)
		}
	}
</script>

<style lang="scss">
	/deep/uni-checkbox:not([disabled]) .uni-checkbox-input:hover {
		border-color: #009000;
	}

	.container {
		.no-content{
			height: calc(100vh - 95px);
		}
		.scroll-list {
			box-sizing: border-box;
			padding: 0 10upx;
			height: calc(100vh - 150px);

			.list {
				padding: 20upx 0 20upx;
				.item {
					display: flex;
					align-items: center;
					justify-content: flex-start;
					background-color: #fff;
					border-radius: 12upx;
					margin-bottom: 20upx;
					padding: 30upx 20upx;
                    &:last-child{
						margin-bottom: 0;
					}
					.content {
						display: flex;
						align-items: flex-start;
						justify-content: flex-start;
						margin-left: 20upx;

						.img {
							width: 150upx;
							height: 150upx;
							border-radius: 12upx;
						}

						.info {
							width: 440upx;
							height: 150upx;
							margin-left: 30upx;
							display: flex;
							flex-direction: column;
							align-items: flex-start;
							justify-content: space-between;

							.name {
								font-size: 30upx;
								font-weight: bold;
								color: $uni-main-color;
								margin-bottom: 6upx;
							}

							.number-box {
								display: flex;
								align-items: center;
								justify-content: space-between;
								width: 100%;

								.price {
									font-size: 28upx;
									font-weight: bold;
									color: #ff557f;
									font-family: '雅黑';
									line-height: 45upx;
								}
							}
						}
					}
				}
			}
		}
		
		/deep/uni-checkbox .uni-checkbox-input {
			width: 35upx;
			height: 35upx;
			border-radius: 50%;
		}

		/* #ifdef MP-WEIXIN */
		.wx-scroll-list {
			height: calc(100vh - 100upx);
		}

		checkbox .wx-checkbox-input {
			width: 35upx;
			height: 35upx;
			border-radius: 50%;
		}

		/* #endif */
		
		.bot-box{
			width: 100%;
			height: 100upx;
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 0 28upx;
			.text-box{
				display: flex;
				align-items: center;
				text{
					font-size: 24upx;
					margin-left: 5upx;
					color: #acacac;
				}
			}
			.r{
				display: flex;
				align-items: center;
				justify-content: flex-start;
				.price-box{
					.text1{
						font-size: 24upx;
						margin-right: 10upx;
					}
					.text2{
						font-size: 26upx;
						font-weight: bold;
						color: #ff557f;
					}
					.price{
						font-size: 32upx;
						font-weight: bold;
						color: #ff557f;
						font-family: '雅黑';
					}
				}
				.btn{
					width: 150upx;
					height: 75upx;
					line-height: 75upx;
					background-color: #009000;
					border-radius: 50upx;
					color: #fff;
					font-size: 35upx;
					margin-left: 20upx;
				}
			}
		}
	}
</style>